@import url('https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300');
@import url('https://fonts.googleapis.com/css?family=Barlow+Condensed|Open+Sans+Condensed:300');
@import url('https://fonts.googleapis.com/css?family=Barlow+Condensed|Barlow+Semi+Condensed|Open+Sans+Condensed:300');
@import url('https://fonts.googleapis.com/css?family=Barlow+Condensed|Barlow+Semi+Condensed|Dosis|Open+Sans+Condensed:300');

body{
    font-family: Arial, Helvetica, sans-serif;
}
header {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 100px;
    @media all and (max-width: 700px) {
        height: 100%;
    }
}
.header_nav{
    display: flex;
    flex-direction: column;
    
}
.social-f {
    display: inline-flex;
    width: 140px;
    justify-content: space-between;
    align-items: center; 
    @media all and (max-width: 700px) {
        display: none;
    }
}
.social-f svg {
      height: 30px;
      width: 30px;
      fill: #fff; 
      &:hover {
        opacity: 1;
        fill: #2d2deb;
    }
}
#slct{
    @media all and (max-width: 500px) {
        min-width: 500px;
    }
}

.header_weather_location {
    background-color: #3c8ed2;
    display: flex;
    justify-content: space-around;
    @media all and (max-width: 500px) {
        flex-direction: column;
    }
}
.current_loc{
    text-decoration: none;
    display: flex;
    color: #ffffff;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.location_text{
    line-height: 50px;
    font-size: 14px;
    
}
.header_weather_period{
    width: 100%;
    background-color: #050000;

    ul.period_list{
        display: flex;
        list-style: none;
        width: 100%;
        justify-content: space-around;
        margin: 14px 0;
        padding: 0;
        transition: all 350ms;
        position: relative;
        

        .period_item {
            position: relative;
            a{
                text-decoration: none;
                color: #fff;
                text-decoration: none;
                color: #fff;
                cursor: pointer;
                font-size: 15px;
                line-height: 20px;
                opacity: 1;
            }
            &::after{
                position: absolute;
                bottom: -8.5px;
                left: 10%;
                width: 80%;
                height: 2px;
                content: '';
                transition: all .35s;
                background-color: #fff;
                transform: scaleX(0);
            }
            &:hover::after{
                transform: scaleX(1);
            }
            
        }
        .period_item:nth-last-child(1){
            @media all and (max-width: 700px) {
                display: none;
            }
        }
        .period_item:nth-last-child(2){
            @media all and (max-width: 500px) {
                display: none;
            }
        }
        .period_item:nth-last-child(3){
            @media all and (max-width: 400px) {
                display: none;
            }
        }
        
    }
}
.wether_penal_wrap{
    position: relative;
    display: flex;
    width: 50%;
    height: 335px;
    z-index: 4000;
    margin: 20px;
    @media all and (max-width: 700px) {
        width: 90%;
    }
}

.wether_penal{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 70%;
    background-color: rgba(88, 88, 241, 0.8);
    position: relative;
    padding: 0 20px;
    background-size: cover;
    
    
}
.wether_penal_title{
    text-align: center;
    text-transform: uppercase;
}
.wether_penal_subtitle{
    text-align: center;
    color: rgba(255, 255, 255, 0.8);
}
.wether_penal_temp {
    padding: 0;
    margin: 0;
    font-size: 45px;
}
.wether_penal p{
    display: inline-block;
}

.wether_penal_icon img{
    position: absolute;
    right: 20px;
    display: inline-block;
    height: 60px;
    width: 60px;
    background-color: rgba(37, 37, 112, 0.8);
}
.wather_list{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 13px;
    list-style: none;
    margin: 0;
    background-color: #ffffff;
    width: 25%;
    margin-left: 10px;
}
.wather_list .wather_item {
    border-bottom: 1px solid #474747;
    padding: 5px 0 0 7px;
    font-size: 14px;
    color: rgb(57, 57, 57);
    height: 100%;
}
.wather_list .wather_item .wather_item_title{
    padding-bottom: 5px;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.wather_list .wather_item .wather_item_subtile{
    margin: 0;
    padding-bottom: 5px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    
    font-weight: 600;
    
}
.wather_list .wather_item > span {

}

.wather_list .wather_item:last-child{
    border-bottom: none;
    @media all and (max-width: 790px) {
        display: none;
    }
}
.wather_list .wather_item:nth-last-child(2){
    @media all and (max-width: 790px) {
        border-bottom: none;
    }
}

$forcast_col: #1f4371;
section.forcast {
    display: flex;
    width: 65%;
    height: 330px;
    flex-direction: column;
    padding: 15px 20px;
    margin: 20px;
    background-color: #ffffff;
    transition: all 300ms ease-in;
    @media all and (max-width: 590px) {
        width: 95%;
        box-sizing: border-box;
        margin: 20px 5px;
        height: 100%;
        .forcast_header{
            h2{
                font-size: 18px;
            }
        }
        .forcast_content{
            flex-wrap: wrap;
        }
    }
    @media all and (max-width: 390px) {
        h2{
            font-size: 16px;
        }
        .forcast_header_link{
            display: none;
        }
    }
        .forcast_header{
            display: flex;
            justify-content: space-between;
            width: 100%;
            height: 40px;
            h2{
                margin: 0;
                color: rgb(57, 57, 57);
            }
            .forcast_header_link{
                position: relative;
                padding-top: 7px;
                a{
                    text-decoration: none;
                    color: #474747;
                    padding-left: 5px;
                }
            }
            .forcast_header_link::before{
                content: '|';
                position: absolute;
                top: 6px;
                left: 87px;
                display: inline-block;
                width: 4px;
                height: 15px;
                color: #000;
            }
            
        }
        .forcast_content{
            display: flex;
            height: 100%;
            .flex_col{
                display: flex;
                flex-direction: column;
                align-items: center;
                min-width: 110px;
                width: 100%;
                padding: 15px 0;
            }
            .period_of_forcast{
                text-align: center;
                text-transform: uppercase;
                font-size: 15px;
                font-weight: 500;
            }
            .forcast_current{
                background-color:$forcast_col;
            }
            .forcast_tomorrow_day{
                background-color:lighten($forcast_col, 10%);
            }
            .forcast_tomorrow_evening{
                background-color:lighten($forcast_col, 15%);
                @media all and(min-width: 400px) and (max-width: 850px) {
                    display: none;
                }
            }
            .forcast_day_after_tomorrow_day{
                background-color:lighten($forcast_col, 20%);
                @media all and (max-width: 670px) {
                    display: none;
                }
            }
            .forcast_in_five_day{
                background-color:lighten($forcast_col, 30%);
            }
        }
        
}
